


export const displayMap=(locations) =>{

    mapboxgl.accessToken = 'pk.eyJ1Ijoic2hvbmdlb3JnZSIsImEiOiJjbG5iZzBlY2YwN2hpMm1scXg2Z2FvbGh3In0.hcUNdupv0dnA0g_yTTYQpQ'

var map = new mapboxgl.Map({
  container: 'map',
  style: 'mapbox://styles/shongeorge/clnbn91pn03l901pbgwz34fx0',
  scrollZoom:false
//   center:[-118.113491,34.111745],
//   zoom:10,
//   interactive:false
});


const bounds =new mapboxgl.LngLatBounds();

locations.forEach(loc => {
    //Create marker
    const el =document.createElement('div');
    el.className='marker';

    //Add marker
    new mapboxgl.Marker({
        element:el,
        anchor: 'bottom'
    })
    .setLngLat(loc.coordinates)
    .addTo(map);

    //Add popup
    new mapboxgl.Popup({
        offset:30
    })
    .setLngLat(loc.coordinates)
    .setHTML(`<p>Day${loc.day}:${loc.description}</p>`)
    .addTo(map)
    //Extend map bounds to include current location
    bounds.extend(loc.coordinates)   
        
});

map.fitBounds(bounds,{
    padding:{
    top:200,
    bottom:150,
    left:100,
    right:100
    }
})

}

